.contacts {
    width: 300px;
    padding: 50px;
    margin-left: auto;
    margin-right: auto;
    border-radius: 7px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    background-color: rgb(235, 235, 235);
}

.contacts li {
    margin-bottom: 10px;
}

.filter input {
    display: block;
    outline: none;
    margin-top: 10px;
    margin-bottom: 15px;
    padding: 7px 15px;
    border-radius: 5px;
    border: 1px solid rgb(187, 187, 187);
    text-align: left;
    margin-bottom: 25px;
}

.btn {
    border: none;
    padding: 0;
    margin-left: 10px;
    display: inline;
}